import { Customer } from '@/types/customer.types';
import Heading from './Heading';
import CircularProgress from '@/components/circles/CircularProgress';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import { SecondaryButton } from '@/components/buttons/SecondaryButton';
import { SubtleButton } from '@/components/buttons/SubtleButton';
import EditIcon from '@/components/icons/EditIcon';
import BlockIcon from '@/components/icons/BlockIcon';
import UnderlineHeading from '@/components/heading/UnderlineHeading';
import { Link } from 'react-router-dom';

interface Props {
    customer: Customer;
    setShowBlockPopup: (value: boolean) => void;
}
export default function CustomerInfo({ customer, setShowBlockPopup }: Props) {
    const handleBlockPopup = () => {
        setShowBlockPopup(true);
    };
    return (
        <div>
            <Heading />
            <div className="flex relative flex-row justify-between px-4 bg-primery-blue-800 w-full text-white-blue-50 rounded-2xl">
                <div className="absolute bottom-0 translate-y-[53%]">
                    <div className="flex flex-col items-center justify-center">
                        <div className="w-[242px] h-[242px] rounded-full overflow-hidden">
                            {customer.profile_img ? (
                                <img
                                    src={customer.profile_img}
                                    alt="Customer Image"
                                    className="w-full h-full object-cover"
                                />
                            ) : (
                                <div className="flex items-center justify-center h-full bg-gray-300">
                                    <p className="text-white-blue-50 text-8xl font-normal tracking-normal">
                                        {customer.full_name
                                            .split(' ')
                                            .map((name) => name[0])
                                            .join('')}
                                    </p>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
                <div className="ms-[242px] ps-6 py-4">
                    <p className="text-start font-bold text-4xl leading-none tracking-normal py-3 mb-3">
                        {customer.full_name}
                    </p>
                    <p className="text-start font-medium text-xl leading-none tracking-normal mt-2 mb-1">عميل</p>
                </div>
                <div className="my-[5.5px] flex flex-col justify-between items-center">
                    <p className="font-medium text-xs tracking-normal">التقييم</p>
                    <CircularProgress
                        textClassName="text-white-blue-50"
                        progressClassName="stroke-white-blue-50"
                        backgroundClassName="stroke-white-blue-700"
                        size={48}
                    />
                    <p className="font-medium text-xs tracking-normal border border-white-blue-50 p-1 rounded-sm">
                        مقبول
                    </p>
                </div>
            </div>
            <div className="flex flex-row">
                <div>
                    <div className="flex flex-col w-[242px] gap-6 ms-4 mt-[150px]">
                        <Link to={`/customer-relations/customer-communication/add-message/${customer.id}`}>
                            <PrimaryButton className="w-full h-[44px]">إرسال رسالة</PrimaryButton>
                        </Link>
                        <Link to={`/customer-relations/edit-customer/${customer.id}`}>
                            <SecondaryButton className="bg-primery-blue-50 h-[44px] w-full">
                                <EditIcon />
                                تعديل
                            </SecondaryButton>
                        </Link>
                        <SubtleButton onClick={handleBlockPopup} className="bg-red-error-50 h-[44px] text-[#A21515]">
                            <BlockIcon />
                            حظر
                        </SubtleButton>
                    </div>
                </div>
                <div className="ms-6 mt-5 w-full">
                    <UnderlineHeading text="البيانات الأساسية" />
                    <div className="w-full mt-2">
                        <div className="flex flex-row items-center border-b border-b-white-blue-500">
                            <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                الاسم الكامل
                            </p>
                            <p className="flex flex-row items-center gap-4 p-2 w-full">{customer.full_name}</p>
                        </div>
                        <div className="flex flex-row items-center border-b border-b-white-blue-500">
                            <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                رقم الهاتف
                            </p>
                            <p className="flex flex-row items-center gap-4 p-2 w-full">{customer.phone}</p>
                        </div>
                        <div className="flex flex-row items-center border-b border-b-white-blue-500">
                            <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                رقم الهوية
                            </p>
                            <p className="flex flex-row items-center gap-4 p-2 w-full">{customer.personal_id_number}</p>
                        </div>
                        <div className="flex flex-row items-center border-b border-b-white-blue-500">
                            <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                العنوان
                            </p>
                            <p className="flex flex-row items-center gap-4 p-2 w-full">{customer?.address}</p>
                        </div>
                        <div className="flex flex-row items-center border-b border-b-white-blue-500">
                            <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                رقم الرخصة
                            </p>
                            <p className="flex flex-row items-center gap-4 p-2 w-full">
                                {customer?.driver_license_number}
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
