import { LoadingSpinner } from '@/components/Loading/LoadingSpinner';
import { useParams } from 'react-router-dom';
import { useEffect, useState } from 'react';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { Customer } from '@/types/customer.types';
import CustomerInfo from '../components/viewCustomer/CustomerInfo';
import PersonalDocuments from '../components/viewCustomer/PersonalDocuments';
import BlockPopup from '@/components/Popups/BlockPopup';
import { toast } from 'sonner';

export default function ViewCustomerPage() {
    const params = useParams();
    const { id } = params;
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [customer, setCustomer] = useState<Customer | null>(null);
    const [showBlockPopup, setShowBlockPopup] = useState(false);

    useEffect(() => {
        const fetchCustomer = async () => {
            try {
                const response = await apiGet({
                    endpoint: endpoints.customer.show + id,
                });
                setCustomer(response.data.data);
            } catch (error) {
                setError('Failed to load customer details');
            } finally {
                setLoading(false);
            }
        };
        if (id) {
            fetchCustomer();
        } else {
            setError('Invalid customer ID');
            setLoading(false);
        }
    }, [id]);

    if (loading) {
        return <LoadingSpinner />;
    }

    if (error) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl text-red-500">{error}</h2>
            </div>
        );
    }

    if (!customer) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl">Customer not found</h2>
            </div>
        );
    }

    const handleCancel = () => {
        setShowBlockPopup(false);
    };

    const handleSuccess = () => {
        toast.error('تم حظر العميل بنجاح');
        setShowBlockPopup(false);
    };
    return (
        <div className="w-full mt-6">
            <CustomerInfo customer={customer} setShowBlockPopup={setShowBlockPopup} />
            <PersonalDocuments customer={customer} />
            {showBlockPopup && (
                <BlockPopup
                    open={showBlockPopup}
                    text1="سيتم حظر العميل"
                    text2="أنت على وشك حظر هذا العميل سيتم حظر  العميل ,ومنعه من عمليات التاجير القادمة، هل أنت متأكد؟"
                    endpoint={endpoints.customer.block + customer.id}
                    handleCancel={handleCancel}
                    handleSuccess={handleSuccess}
                />
            )}
        </div>
    );
}
