import { DataTable } from '@/components/tables/customTable/data-table';
import { endpoints } from '@/lib/axios/axios-global';
import { apiGet } from '@/lib/axios/axios-utils';
import { useCallback, useState } from 'react';
import { columns } from './rentsRegisterTable/columns';
import { DataTableToolbar } from './rentsRegisterTable/data-table-toolbar';
import { FetchDataOptions } from '@/types/table.types';
import { Customer } from '@/types/customer.types';
import { Car } from '@/types/cars.types';

interface Props {
    customer: Customer;
}
export default function RentsRegister({ customer }: Props) {
    const [data, setData] = useState<Car[]>([]);
    const [pageCount, setPageCount] = useState(0);
    const [totalCount, setTotalCount] = useState(0);
    const [isLoading, setIsLoading] = useState(false);

    // Use useCallback to ensure the function reference is stable
    const fetchData = useCallback(async (options: FetchDataOptions) => {
        setIsLoading(true);
        try {
            // const response = await apiGet({
            //     endpoint: endpoints.branch.list,
            //     queryParams: { page: options.pageIndex + 1 , ...options.filters, search: options.globalFilter },
            // });
            // setData(response.data.data.data);
            // setPageCount(Math.ceil(response.data.data.pagination.total / 10));
            // setTotalCount(response.data.data.pagination.total);
        } catch (error) {
            console.error('Error fetching data:', error);
        } finally {
            setIsLoading(false);
        }
    }, []);

    return (
        <div className="mb-10 mt-4 rounded-lg border border-[#E8E7EA] shadow-[0px_4px_4px_-1px_#0C0C0D0D,0px_4px_4px_-1px_#0C0C0D1A]">
            <DataTable
                columns={columns}
                data={data}
                pageCount={pageCount}
                totalCount={totalCount}
                onFetchData={fetchData}
                DataTableToolbar={DataTableToolbar}
                isLoading={isLoading}
            />
        </div>
    );
}
