import { SubtleButton } from '@/components/buttons/SubtleButton';
import UnderlineHeading from '@/components/heading/UnderlineHeading';
import DocumentIcon from '@/components/icons/DocumentIcon';
import { Customer } from '@/types/customer.types';
import RentsRegister from './RentsRegister';

interface Props {
    customer: Customer;
}

export default function PersonalDocuments({ customer }: Props) {
    return (
        <div className="w-full mt-5">
            <div className="flex flex-row justify-between mb-5">
                <UnderlineHeading text="المستدات الشخصية" />
                <SubtleButton>
                    <DocumentIcon className="text-[#616161]" />
                    تحميل المستندات
                </SubtleButton>
            </div>
            <div className="grid grid-cols-1 gap-6 md:grid-cols-2 mb-[21px]">
                <div>
                    <p className="text-[#100147] mb-3 font-medium text-[18px] leading-none tracking-normal text-start">
                        الهوية الشخصيه
                    </p>
                    <div className="mb-4 h-[240px] border-2 border-dashed border-[#B6B5BF] rounded-lg p-8">
                        <img
                            className="w-full h-full object-contain"
                            src={customer?.personal_id_img_front}
                            alt="personal id img front"
                        />
                    </div>
                    <div className="flex flex-row gap-5">
                        <div className="w-full h-[107px] border-2 border-dashed border-[#B6B5BF] rounded-lg p-8">
                            <img
                                className="w-full h-full object-contain"
                                src={customer?.personal_id_img_front}
                                alt="personal id img front"
                            />
                        </div>
                        <div className="w-full h-[107px] border-2 border-dashed border-[#B6B5BF] rounded-lg p-8">
                            <img
                                className="w-full h-full object-contain"
                                src={customer?.personal_id_img_back}
                                alt="personal id img back"
                            />
                        </div>
                    </div>
                </div>
                <div>
                    <p className="text-[#100147] mb-3 font-medium text-[18px] leading-none tracking-normal text-start">
                        رخصة القيادة
                    </p>
                    <div className="mb-4 h-[240px] border-2 border-dashed border-[#B6B5BF] rounded-lg p-8">
                        <img
                            className="w-full h-full object-contain"
                            src={customer?.driver_license_img_back}
                            alt="driver license img back"
                        />
                    </div>
                    <div className="flex flex-row gap-5">
                        <div className="w-full h-[107px] border-2 border-dashed border-[#B6B5BF] rounded-lg p-8">
                            <img
                                className="w-full h-full object-contain"
                                src={customer?.driver_license_img_back}
                                alt="driver license img back"
                            />
                        </div>
                        <div className="w-full h-[107px] border-2 border-dashed border-[#B6B5BF] rounded-lg p-8">
                            <img
                                className="w-full h-full object-contain"
                                src={customer?.driver_license_img_back}
                                alt="driver license img back"
                            />
                        </div>
                    </div>
                </div>
            </div>
            <RentsRegister customer={customer} />
        </div>
    );
}
