import { Table } from '@tanstack/react-table';
import UnderlineHeading from '@/components/heading/UnderlineHeading';
import { CustomSelect } from '@/components/dropDown/Dropdown';
import { Branch } from '@/types/branches.types';
import CloseIcon from '@/components/icons/CloseIcon';
import { Filters } from '@/types/table.types';

interface DataTableToolbarProps {
    table: Table<Branch>;
    globalFilter: string;
    setGlobalFilter: (value: string) => void;
    filters: Filters;
    setFilters: (value: Filters) => void;
}

export function DataTableToolbar({ table, globalFilter, setGlobalFilter, filters, setFilters }: DataTableToolbarProps) {
    return (
        <>
            <div className="flex flex-col p-2 sm:flex-row sm:items-center sm:justify-between">
                <UnderlineHeading text="سجل التأجيرات" />
                <div className="flex flex-row items-center gap-4">
                    <div className="flex fle-row items-center gap-4">
                        <CustomSelect
                            value={(filters?.status as string) || ''}
                            onValueChange={(value) => {
                                setFilters({ ...filters, status: value });
                            }}
                            placeholder="الحالة"
                            width="min-w-[77px]"
                            options={[
                                { label: 'متاح', value: 'active' },
                                { label: 'غير متاح', value: 'not active' },
                            ]}
                        />
                        <button
                            onClick={() => setFilters({})}
                            className="flex flex-row items-center gap-2 h-[22px] p-0 bg-white-blue-50 hover:bg-white-blue-200 border-none shadow-none text-primery-blue-500 font-medium text-xs leading-[22.49px] tracking-normal"
                        >
                            <CloseIcon className="!w-[10px] !h-[10px]" />
                            إلغاء التصفية
                        </button>
                    </div>
                    {/* <div className="flex items-center justify-center gap-2 px-[14px] py-2 bg-primery-blue-50 border border-primery-blue-50 shadow-[0px_1px_2px_0px_#1018280D] rounded-lg ">
                        <ArrowBackIcon className="text-primery-blue-700" />
                        <p className="text-xs text-primery-blue-500 tracking-normal font-normal leading-[22.49px]">
                            تصدير
                        </p>
                    </div> */}
                </div>
            </div>
            {/* <DataTableViewOptions table={table} /> */}
        </>
    );
}
