import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { apiGet } from '@/lib/axios/axios-utils';
import { cn } from '@/lib/utils';
import { useState } from 'react';
import { toast } from 'sonner';
import BlockUserIcon from '../icons/user/BlockUserIcon';

interface Props {
    open: boolean;
    endpoint: string;
    text1: string;
    text2: string;
    handleCancel: () => void;
    handleSuccess: () => void;
}

const BlockPopup = ({ open, text1, text2, endpoint, handleCancel, handleSuccess }: Props) => {
    const [loading, setLoading] = useState(false);

    const handleBlock = async () => {
        setLoading(true);

        try {
            await apiGet({
                endpoint,
            });

            handleSuccess();
        } catch (error: any) {
            // Handle error
            let errorMessage = 'حدث خطأ أثناء إرسال البيانات. يرجى المحاولة مرة أخرى.';

            if (error.response) {
                // Server responded with error status (4xx, 5xx)
                errorMessage = error.response.data.msg || errorMessage;
            } else if (error.request) {
                // Request was made but no response received
                errorMessage = 'تعذر الاتصال بالخادم. يرجى التحقق من اتصالك بالإنترنت.';
            }
            toast(errorMessage);
        } finally {
            setLoading(false);
        }
    };
    return (
        <Dialog open={open}>
            <div
                className={cn('fixed inset-0 bg-black/50 z-[999]', {
                    '!hidden': !open,
                })}
            />
            <DialogContent
                showCloseButton={false}
                className="z-[1000] max-w-lg rounded-lg p-6 bg-white-blue-50 font-cairo gap-0 [&_[aria-label=Close]]:opacity-0"
            >
                <DialogHeader className="flex flex-col items-center">
                    <div className="p-1 mb-4">
                        <BlockUserIcon />
                    </div>
                    <DialogTitle className="text-center text-red-error-600 font-bold text-[22px] leading-[33px] tracking-normal">
                        {text1}
                    </DialogTitle>
                </DialogHeader>

                <div className="mt-2 mb-6 text-center text-[#6B7280] font-medium text-[14px] leading-[28px] tracking-normal">
                    <p>{text2}</p>
                </div>

                <div className="flex flex-row gap-3">
                    <PrimaryButton
                        onClick={() => handleBlock()}
                        className="w-full h-10 text-base text-red-error-600 hover:text-white-blue-50 bg-red-error-50 hover:bg-red-error-600 leading-5 tracking-normal font-medium py-[10px]"
                    >
                        {loading ? 'جاري الحظر...' : 'حظر'}
                    </PrimaryButton>
                    <PrimaryButton
                        onClick={handleCancel}
                        className="w-full h-10 text-base leading-5 tracking-normal py-[10px] font-medium border border-red-error-50"
                    >
                        العودة لقائمة بيانات العميل
                    </PrimaryButton>
                </div>
            </DialogContent>
        </Dialog>
    );
};

export default BlockPopup;
